.skills{
    position: relative;
   // min-height: calc(100vh - 80px);
    padding-bottom: 5%;
    background-color: $dark1;
    text-align: center;
    color: $light-color;
    &__title-section {
        padding: 20px 0;
        font-size: 25px;    
    }

    &__sections{
        @include flexy($dir:row,$align:center,$just:space-around);
        margin-bottom: 2em;
        img{
            width: 200px;
            height: auto;
            padding-bottom: 10px;
        }

        &--design{
            h2{
                padding: 1em 0;
            }
            margin: 5px;
            @include flexy($dir:column);
            &--images{
                @include flexy($dir: row,$align:center,$just:center, $wrap: wrap);

                &-img1{
                    margin: 15px;
                }
            }
        }
    }

    &__divider{
        position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
    svg {
        position: relative;
        display: block;
        width: calc(100% + 1.3px);
        height: 50px;
        @include mq('md'){
            height: 100px;
        }
    }
    &--fill{
        fill: $darkShades; 
    }
    }
}