.contact {
  position: relative;
  min-height: calc(90vh - 80px);
  background-color: $dark1;
  text-align: center;
  color: $light-color;

  &__titleSection{
    padding: 20px 0;
    font-size: 25px;

    p{
      font-size: 16px;
      color: $blueMainColor;
      padding: 20px 0;
    }
  }

  &__emailForm{
    @include flexy($dir:column, $align:center, $just:center);
    position: relative;
    margin-bottom: 10%;
    input{
      width: 300px;
      padding: 7px;
      background-color: $darkShades;
      color: $light-color;
      border: none;
      margin: 0 0 3px;
      outline: none;
    }
    textarea{
      width: 300px;
      height: 150px;
      padding: 7px;
      background-color: $darkShades;
      color: $light-color;
      border: none;
      outline: none;
      resize: vertical;
    }
    button{
      position: absolute;
      padding: 10px 20px;
      bottom: -50px;
      transform: translateX(98px);
      background-color: $dark1;
      border: 2px solid $light-color;
      border-radius: 3px;
      color: $light-color;
      text-transform: uppercase;
    }

    @include mq('md'){
      input, textarea{
        width: 500px;
      }
      button{
        transform: translateX(198px);
      }
    }
  }

  &__divider {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    svg {
      position: relative;
      display: block;
      width: calc(115% + 1.3px);
      height: 80px;
      @include mq("md") {
        height: 140px;
      }
      @include mq("lg") {
        height: 200px;
      }
    }
    &--fill {
      fill: $darkShades;
    }
  }
}
